/* important здесь используется, чтобы срабатывал visually-hidden для элементов
с любыми заданными параметрами, ниже в коде или с более приоретными свойствами*/
.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
